.page-header {
  background-color: $colour-logo-background;
  color: $white;
  height: $header-height-min;
  @media (min-width: 730px) {
    height: $header-height-max;
  }

  .container {
    height: 100%;
  }
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  height: 100%;
  flex-direction: column;
  @media (min-width: 530px) {
    flex-direction: row;
  }
}

/* Logo */
.logo-container {
  height: 100%;
  display: flex;
  align-items: center;

  .logo {
    background: url('../img/logo.png') no-repeat center center;
    background-size: contain;
    height: 100%;
    width: 200px;
    text-indent: -9999px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    @media (min-width: 530px) {
      width: 180px;
      margin-top: 0;
    }
    @media (min-width: 730px) {
      width: 342px;
    }
  }
}

/* Navbar */
.navbar {
  display: flex;
  height: 100%;
  align-items: center;
  @media (min-width: 530px) {
    width: 423px;
  }

  ul {
    width: 100%;
    display: flex;
    justify-content: space-between;
    list-style: none;
    margin-top: 20px;
    @media (min-width: 530px) {
      margin-top: 0;
    }

    li a {
      @extend %heading-font;

      @media (max-width: 530px) {
        font-size: 18px;
      }
    }
  }
}
