.our-work {
  .our-work-links {
    height: $our-work-links-height;
    background-color: $colour-logo-background;
    color: white;

    border-top: $our-work-links-border-height solid $colour-logo;

    .our-work-links__list {
      display: flex;
      justify-content: space-around;
      list-style: none;
      height: 100%;
      align-items: center;
    }

    .our-work-link__list-item a {
      display: block;

      div {
        text-align: center;
      }
    }

    .container {
      height: 100%;
    }

    .our-work-link__image {
      img {
        height: 30px;
        @media (min-width: 530px) {
          height: 41px;
        }
      }
    }
    .our-work-link__text {
      @media (max-width: 529px) {
        font-size: 12px;
      }
    }
  }

  .our-work__option {
    padding: $gutter 0;

    header {
      padding: 10px 0;
    }
  }

  .our-work__section-divider {
    height: $our-work-section-divider-height;
  }

  .images {
    //column-count: 3;
    //column-gap: 15px;
    //column-fill: auto;
    font-size: 0;
  }

  .image {
    display: inline-block;
    border: 2px solid #FAFAFA;
    box-shadow: 0 1px 2px rgba(34, 25, 25, 0.4);
    width: 100%;
    margin-bottom: 15px;
    //column-break-inside: avoid;
    padding: 15px;
    padding-bottom: 5px;

    //The following two rules are not for the masonry columns layout
    vertical-align: top;
    @include medium-and-up {
      width: calc(50% - 20px); //33% width subtract the 10px margin on either side
      margin: 0 10px 15px;
    }
    @include large-and-up {
      width: calc(33% - 20px); //33% width subtract the 10px margin on either side
      margin: 0 10px 15px;
    }

    img {
      width: 100%;
      height: 100%;
    }
  }
}

//Parallax separators
.our-work__section-divider--first {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../img/separators/1_mobile.jpg');
  @media(min-width: 697px) {
    background-image: url('../img/separators/1_720p.jpg');
  }
  @media(min-width: 1366px) {
    background-image: url('../img/separators/1_2k.jpg');
  }
}
.our-work__section-divider--second {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../img/separators/2_mobile.jpg');
  @media(min-width: 697px) {
    background-image: url('../img/separators/2_720p.jpg');
  }
  @media(min-width: 1366px) {
    background-image: url('../img/separators/2_2k.jpg');
  }
}
