.request-consultation_background-overlay {
  &.off-canvas {
    display: none;
    & + .request-consultation-modal {
      display: none;
    }
    & + .request-consultation-modal + .request-consultation-button-container {
      display: block;
    }
  }

  /* Hide the Request Consultation button when the modal is showing */
  & + .request-consultation-modal + .request-consultation-button-container {
    display: none;
  }
}

.request-consultation_background-overlay {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 10;
}
.request-consultation-modal {
  position: fixed;
  z-index: 11;
  top: 40%;
  left: 50%;
  margin-left: -$request-consultation-modal-width/2;
  margin-top: -$request-consultation-modal-height/2;
  height: $request-consultation-modal-height;
  width: $request-consultation-modal-width;
  background-color: $colour-logo-background;
  color: $white;
  padding: $gutter;
  box-sizing: border-box;
}

.request-consultation-modal__header {
  display: flex;
  justify-content: space-between;
}

.request-consultation-modal__close-button {
  padding: 0;
  margin: 0;
  height: 30px;
  width: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  color: $white;
  margin: -10px -10px 0 0;
  &:hover {
    color: $colour-logo;
  }
}

#request-consultation-form {
  input, textarea {
    background-color: $colour-form-field-background;
    border: none;
    color: white;
    border-radius: 0;
  }

  textarea {
    height: $request-consultation-form-textarea-height;
    resize: none;
  }

  .request-consultation__submit-container {
    text-align: right;

    input {
      &:hover {
        color: $colour-logo;
      }
    }
  }
}

.request-consultation-button-container {
  position: absolute;
  right: 0;
  top: $header-height-min + $hero-height + $our-work-links-height + $our-work-links-border-height + 20px;

  @media (min-width: 730px) {
    top: $header-height-max + $hero-height + $our-work-links-height + $our-work-links-border-height + 20px;
  }
  .request-consultation-button {
    @extend %link-font;
    background-color: $colour-baseline-footer;
    line-height: inherit;
    width: 150px;
    height: $request-consultation-button-height;
    white-space: pre-wrap;
    border: none;
    &:hover {
      color: $colour-logo;
    }
  }
}
