.page_footer {
  padding-top: $gutter;
  min-height: $footer-height;
  background-color: $colour-logo-background;
  color: $white;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  //Getting specific so the font size doesn't affect the baseline footer
  & > section .container {
    font-size: 0;
  }

  .baseline-footer {
    height: $baseline-footer-height;
    background-color: $colour-baseline-footer;
    width: 100%;
    text-align: right;
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-top: $gutter;
  }

  .about-us-column {
    width: 100%;
    text-align: center;
    @media (min-width: 730px) {
      width: 50%;
      text-align: left;
    }
    @media (min-width: 1200px) {
      width: 66.66%
    }

  }

  .contact-column {
    width: 100%;
    padding-top: 20px;
    text-align: center;
    @media (min-width: 730px) {
      padding-top: 0;
      width: 50%;
      text-align: right;
    }
    @media (min-width: 1200px) {
      width: 33.33%;
    }
  }

  .about-us-column,
  .contact-column {
    display: inline-block;
    vertical-align: top;
  }

  .contact-us {
    /* Don't want the italics styling that comes along with the <address> tag */
    address {
      font-style: normal;
    }
  }

  p {
    margin: 0;
  }
}
